body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes {
  z-index: 2;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: rotation-arrow;
  animation-duration: 3600s;
  animation-timing-function: steps(60, start);
  animation-iteration-count: infinite;
  animation-delay: 60s;
  position: absolute;
  top: 25%;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: rotation-arrow;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 2%;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
  animation-delay: 10s;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}

@keyframes rotation-arrow {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
